var Fighters = (function () {
    function Fighters(id, name, normimage, baseHP, baseDamage, range, resourceRepo, armyVitality) {
        this.ID = id;
        this.Name = name;
        this.NormImage = normimage;
        this.BaseHP = baseHP;
        this.BaseDamage = baseDamage;
        this.ResourceRepo = resourceRepo;
        this.Range = range;
        this.ArmyVitality = armyVitality;
        this.HP = baseHP * this.ArmyVitality;
        this.Damage = baseDamage * this.ArmyVitality;
    }
    ;
    Fighters.prototype.Exist = function () {
        console.log(this.ID + this.Name + this.NormImage + this.BaseDamage + this.ArmyVitality + this.Damage + this.ResourceRepo + this.Range);
    };
    ;
    Fighters.prototype.Unexist = function () {
    };
    ;
    Fighters.prototype.Unlocked = function () { };
    ;
    Fighters.prototype.Hurt = function (Alive, Damage) { };
    ;
    Fighters.prototype.GetHurt = function (Damage) { };
    ;
    Fighters.prototype.Regenerate = function (Health) {
        this.HP += Health;
        this.HP = Math.min(this.HP, this.BaseHP * this.ArmyVitality);
    };
    ;
    Fighters.prototype.UpdateStatus = function (currentTime) {
        if (currentTime % 20) {
            this.Regenerate(1);
            console.log("+ 1 HP");
        }
    };
    return Fighters;
}());
export { Fighters };
